import React, { useContext, useState } from 'react'
import { Context } from '..'
import { Button, Col, Container, Form, ListGroup, Modal, ModalTitle, Row } from 'react-bootstrap'
import { getallNumbers } from '../http/numbersAPI'
import { deleteServicebyID, getAllServices, postService } from '../http/serviceAPI'
import ModalPopup from './page-components/modals/modal-popup'

function PageAdmin() {
    const context = useContext(Context)
    const isAuth = context.user.isAuth

    const [name, setName] = useState('')
    const [price, setPrice] = useState('')
    const [imgUrl, setImgUrl] = useState('')

    const [serviceId, setServiceId] = useState(0)

    const [numbers, setNumbers] = useState([])
    const [showNumbers, setShowNumbers] = useState(false)
    const [services, setServices] = useState([])
    const [showServices, setShowServices] = useState(false)

    const [showModal1, setShowModal1] = useState(false);
    const [showModal2, setShowModal2] = useState(false)
    const [showModalWarn, setShowModalWarn] = useState(false)

  const handleClose = () => {window.location.reload()}
  const handleShow1 = () => {setShowModal1(true)}
  const handleShow2 = () => {setShowModal2(true)}

    const getNumbers = async () => {
       setNumbers((await (getallNumbers())).data)
       setShowNumbers(true)
       setShowServices(false)
    }
    const getServices = async () => {
        setServices((await (getAllServices())).data)
        setShowNumbers(false)
        setShowServices(true)
    }

    const handleSubmitService = async () => {
        const response = await postService(name, price, imgUrl)
        if (response.data.ServicesStatus) {
            window.location.reload();
        } else {
            setShowModalWarn(true)
        }
    }
    const handleDeleteService = async () => {
        console.log(typeof(serviceId));
        var pasreId = parseInt(serviceId, 10)
        const response = await deleteServicebyID(pasreId)
        console.log(parseInt(serviceId, 10));
        if (response.data.ServicesStatus) {
            console.log(response.data.ServicesStatus);
            window.location.reload();
        } else {
            setShowModalWarn(true)
        }
    }

  return (
    <Container>
        {isAuth === 'true' ?
        <> 
        <Row className='d-flex align-items-center'>
            <svg style={{width:'50px', height:'50px', color:'#99252b', padding:'0px'}}  xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
  <path strokeLinecap="round" strokeLinejoin="round" d="M9.594 3.94c.09-.542.56-.94 1.11-.94h2.593c.55 0 1.02.398 1.11.94l.213 1.281c.063.374.313.686.645.87.074.04.147.083.22.127.324.196.72.257 1.075.124l1.217-.456a1.125 1.125 0 011.37.49l1.296 2.247a1.125 1.125 0 01-.26 1.431l-1.003.827c-.293.24-.438.613-.431.992a6.759 6.759 0 010 .255c-.007.378.138.75.43.99l1.005.828c.424.35.534.954.26 1.43l-1.298 2.247a1.125 1.125 0 01-1.369.491l-1.217-.456c-.355-.133-.75-.072-1.076.124a6.57 6.57 0 01-.22.128c-.331.183-.581.495-.644.869l-.213 1.28c-.09.543-.56.941-1.11.941h-2.594c-.55 0-1.02-.398-1.11-.94l-.213-1.281c-.062-.374-.312-.686-.644-.87a6.52 6.52 0 01-.22-.127c-.325-.196-.72-.257-1.076-.124l-1.217.456a1.125 1.125 0 01-1.369-.49l-1.297-2.247a1.125 1.125 0 01.26-1.431l1.004-.827c.292-.24.437-.613.43-.992a6.932 6.932 0 010-.255c.007-.378-.138-.75-.43-.99l-1.004-.828a1.125 1.125 0 01-.26-1.43l1.297-2.247a1.125 1.125 0 011.37-.491l1.216.456c.356.133.751.072 1.076-.124.072-.044.146-.087.22-.128.332-.183.582-.495.644-.869l.214-1.281z" />
  <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
</svg>
            <Col xs={4} className=''>
                <h1 className='m-0 p-0' style={{fontFamily:'Inter', fontWeight:'bold', color:'#99252b'}}> </h1>        
            </Col>
            <Col className='p-2'>
                <Button onClick={getNumbers} variant='outline-dark'>  <svg style={{width:'25px', height:'25px'}} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
    <path strokeLinecap="round" strokeLinejoin="round" d="M10.5 1.5H8.25A2.25 2.25 0 006 3.75v16.5a2.25 2.25 0 002.25 2.25h7.5A2.25 2.25 0 0018 20.25V3.75a2.25 2.25 0 00-2.25-2.25H13.5m-3 0V3h3V1.5m-3 0h3m-3 18.75h3" />
    </svg>
    </Button>
                <Button onClick={getServices} variant='outline-dark'>  <svg style={{width:'25px', height:'25px'}} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
  <path strokeLinecap="round" strokeLinejoin="round" d="M3.375 19.5h17.25m-17.25 0a1.125 1.125 0 01-1.125-1.125M3.375 19.5h7.5c.621 0 1.125-.504 1.125-1.125m-9.75 0V5.625m0 12.75v-1.5c0-.621.504-1.125 1.125-1.125m18.375 2.625V5.625m0 12.75c0 .621-.504 1.125-1.125 1.125m1.125-1.125v-1.5c0-.621-.504-1.125-1.125-1.125m0 3.75h-7.5A1.125 1.125 0 0112 18.375m9.75-12.75c0-.621-.504-1.125-1.125-1.125H3.375c-.621 0-1.125.504-1.125 1.125m19.5 0v1.5c0 .621-.504 1.125-1.125 1.125M2.25 5.625v1.5c0 .621.504 1.125 1.125 1.125m0 0h17.25m-17.25 0h7.5c.621 0 1.125.504 1.125 1.125M3.375 8.25c-.621 0-1.125.504-1.125 1.125v1.5c0 .621.504 1.125 1.125 1.125m17.25-3.75h-7.5c-.621 0-1.125.504-1.125 1.125m8.625-1.125c.621 0 1.125.504 1.125 1.125v1.5c0 .621-.504 1.125-1.125 1.125m-17.25 0h7.5m-7.5 0c-.621 0-1.125.504-1.125 1.125v1.5c0 .621.504 1.125 1.125 1.125M12 10.875v-1.5m0 1.5c0 .621-.504 1.125-1.125 1.125M12 10.875c0 .621.504 1.125 1.125 1.125m-2.25 0c.621 0 1.125.504 1.125 1.125M13.125 12h7.5m-7.5 0c-.621 0-1.125.504-1.125 1.125M20.625 12c.621 0 1.125.504 1.125 1.125v1.5c0 .621-.504 1.125-1.125 1.125m-17.25 0h7.5M12 14.625v-1.5m0 1.5c0 .621-.504 1.125-1.125 1.125M12 14.625c0 .621.504 1.125 1.125 1.125m-2.25 0c.621 0 1.125.504 1.125 1.125m0 1.5v-1.5m0 0c0-.621.504-1.125 1.125-1.125m0 0h7.5" />
</svg>
</Button>
                <Button onClick={handleShow1} variant='outline-success'>  <svg style={{width:'25px', height:'25px'}} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
    <path strokeLinecap="round" strokeLinejoin="round" d="M19.5 14.25v-2.625a3.375 3.375 0 00-3.375-3.375h-1.5A1.125 1.125 0 0113.5 7.125v-1.5a3.375 3.375 0 00-3.375-3.375H8.25m3.75 9v6m3-3H9m1.5-12H5.625c-.621 0-1.125.504-1.125 1.125v17.25c0 .621.504 1.125 1.125 1.125h12.75c.621 0 1.125-.504 1.125-1.125V11.25a9 9 0 00-9-9z" />
    </svg>
    </Button>
                <Button onClick={handleShow2} variant='outline-danger'>  <svg style={{width:'25px', height:'25px'}} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
  <path strokeLinecap="round" strokeLinejoin="round" d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0" />
</svg>
</Button>
                <Button variant='outline-danger'>  <svg style={{width:'25px', height:'25px'}} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
  <path strokeLinecap="round" strokeLinejoin="round" d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0" />
</svg>
</Button>
            </Col>
            <hr></hr>
        </Row>
        <Row>
            <Col>
                <ListGroup>
                    { showNumbers === true ?
                         numbers.map((number, index) => {
                            console.log(number);
                            return <ListGroup.Item key={index}>
                                        <h2>ID: {number.id}</h2>
                                        <p> :{number.phonenumber}</p>
                                        <p> : {number.createdAt}</p>
                            </ListGroup.Item>
                    })
                    :
                    <></>
                    }

                    { showServices === true ?
                    services.map((service,index) => {
                        return <ListGroup.Item key={index}>
                                    <h2>ID: {service.id}</h2>
                                    <p>: {service.service_name}</p>
                                    <p>: {service.service_price}</p>
                                    <p>: {service.service_img_url}</p>
                                    <p> : {service.createdAt}</p>
                        </ListGroup.Item>
                    })
                    :
                    <></>
                    }

                    {showModal1 === true ?
                        <Modal show={showModal1} onHide={handleClose} centered>
                            <Modal.Header closeButton><h3>  </h3></Modal.Header>
                            <Modal.Body>
                                <Form>
                                    <Form.Group>
                                        <Form.Label></Form.Label>
                                        <Form.Control value={name} onChange={e => setName(e.target.value)} placeholder=''></Form.Control>
                                    </Form.Group>
                                    <Form.Group>
                                        <Form.Label></Form.Label>
                                        <Form.Control value={price} onChange={e => setPrice(e.target.value)} placeholder=''></Form.Control>
                                    </Form.Group>
                                    <Form.Group>
                                        <Form.Label></Form.Label>
                                        <Form.Control value={imgUrl} onChange={e => setImgUrl(e.target.value)} placeholder='URL'></Form.Control>
                                    </Form.Group>
                                </Form>
                            </Modal.Body>
                            <Modal.Footer>
                                <Button variant='success' onClick={handleSubmitService}></Button>
                                <Button variant='danger' onClick={handleClose}></Button>
                            </Modal.Footer>
                        </Modal>
                    :
                    <></>
                    }

                    { showModalWarn ? 
                    <ModalPopup  heading={`!`} body={` `} show={true}/>
                    :
                    <></>
                    }

                    { showModal2 ?
                        <Modal show={showModal2} onHide={handleClose} centered>
                            <Modal.Header closeButton><h3> </h3></Modal.Header>
                            <Modal.Body>
                                <Form>
                                    <Form.Group>
                                        <Form.Label>ID</Form.Label>
                                        <Form.Control value={serviceId} onChange={e => setServiceId(e.target.value)} placeholder='1'></Form.Control>
                                    </Form.Group>
                                </Form>
                            </Modal.Body>
                            <Modal.Footer>
                                <Button variant='success' onClick={handleDeleteService}></Button>
                                <Button variant='danger' onClick={handleClose}></Button>
                            </Modal.Footer>
                        </Modal> 
                    :
                    <></>
                    }
                </ListGroup>
            </Col>
        </Row>
        </> 
        : 
        <>
        <ModalTitle className='text-center'>405 No Access</ModalTitle>
        <p className='text-center font-italic'> </p>
        </>}
    </Container>
  )
}

export default PageAdmin