import React from 'react'
import { Card } from 'react-bootstrap'

function ServiceCard(service) {
    console.log(service)
  
    const blya = service.service
    console.log(blya);
    console.log(blya.length);
  return (
    <Card 
    style={{
    width:'300px', 
    marginTop:'20px'}}>
        <Card.Body>
            <Card.Img src={blya.service_img_url} variant='top'></Card.Img>
            <Card.Title>
              {blya.service_name}
            </Card.Title>
         </Card.Body>
        <Card.Footer>
                 {blya.service_price}  .
        </Card.Footer>
    </Card>
  )
}

export default ServiceCard
