import { $host } from "./index";

const getallNumbers = async () => {
    const response = await $host.get('api/Numbers')
    console.log(response);
    return response
}

const getNumbersbyID = async (id) => {
    const response = await $host.get('api/Numbers', {id})
    console.log(response);
    return response
}

const postNumbers = async (phonenumber) => {
    const response = await $host.post('api/Numbers', {phonenumber})
    console.log(response);
    return response
}

export {
    getallNumbers,
    getNumbersbyID,
    postNumbers
}