import React, {useEffect, useState} from 'react';
import { Col, Container, ListGroup } from 'react-bootstrap';
import { Row } from 'react-bootstrap';
import NumberCatcher from './page-components/component-number-catcher';
import { getAllServices } from '../http/serviceAPI';
import ServiceCard from './page-components/component-service-card';

function PageServices() {
    const [serviceArray, setServiceArray] = useState([]);

    const getService = async () => {
      setServiceArray((await getAllServices()).data)
    };
   
   useEffect(() => {
      getService()
    },[]);
    

    return ( 
        <Container>
            <NumberCatcher
            url={`https://www.artur-servis.ru/upload/medialibrary/f9c/f9c5338086f252f7f3acea4cf1baae2b.jpg`}
            header1={`   `}
            header2={`      `}/>
    
            <h1 style={{marginTop:'20px', color:'#99252b'}}> </h1>
            <Row style={{marginTop:'20px'}}>
                <Col >
                  <ListGroup>
                    <ListGroup.Item className='md-3 flex-wrap d-flex align-items-center justify-content-around' >
                  {  serviceArray.map((service, ind) => {

                    
                    
                        return <ServiceCard key={ind} index={ind} service={service} />
                         }
                    
                  )}
                    </ListGroup.Item>
                  </ListGroup> 
                </Col>
            </Row>
        </Container>
        
     );
};

export default PageServices;