import React from 'react'
import PropTypes from 'prop-types'
import Row from 'react-bootstrap/Row'; 
import Col from 'react-bootstrap/Col';
import { Button, Form, FormGroup } from 'react-bootstrap';
import { useState } from 'react';
import { postNumbers } from '../../http/numbersAPI';
import ModalPopup from './modals/modal-popup';


function NumberCatcher(props) {
  const [number, setNumber] = useState('')
  const [isModal, setModal] = useState(false)
  const [isModalWarn, setModalWarn] = useState(false)

  const sendNumbers = async () => {
    const response = await postNumbers(number)
    if (response.data.NumberStatus) {
      setModal(true)
    } else {
      setModal(false)
      setModalWarn(true)
    }
    console.log(response);
  }

  return (
    <Row>
      <Col style={{position: 'relative', marginTop:'30px'}}>
        <div style={{backgroundImage:`linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(${props.url})`, borderRadius:'10px 10px 10px 10px / 10px 10px 10px 10px', backgroundSize:'cover', width:'100%', height:'400px', filter:'blur(2px)'}}>
        </div>
        <p className='EnterCompanyText' style={{position: 'absolute', bottom: '50%', left: '10%', right: '20%'}}>{props.header1}</p>
        <h2 className='EnterCompanyText' style={{position: 'absolute', bottom: '40%', left: '10%', right: '20%', fontSize:'18pt'}}>{props.header2}</h2>
        <Button 
        onClick={sendNumbers} 
        style={{position: 'absolute', bottom: '25%', left: '45%', right: '40%'}} 
        variant="outline-light"> !</Button>
        <FormGroup><Form.Control 
        onChange={e => setNumber(e.target.value)}  
        style={{position: 'absolute', bottom: '25%', left: '10%', right: '20px', width:'30%'}} 
        variant="outline-light" type='tel' minLength={11} 
        placeholder="  " /></FormGroup>
        
      </Col>
      {isModal ? <ModalPopup heading={`!`} body={`   ,      3  `} show={true}/>
      : <div></div>
      }
      
      { isModalWarn ?
      <ModalPopup heading={`!`} body={` ,   `} show={true}></ModalPopup>
      :
      <></>}
    </Row>
  )
}

NumberCatcher.propTypes = {optionalString: PropTypes.string}

export default NumberCatcher
