import React, { useContext, useState }/* , { useEffect, useState } */ from 'react';
import { Container, Form } from 'react-bootstrap';
import { Button, Card } from 'react-bootstrap';
import { Context } from '..';
import { login } from '../http/userAPI';
import { useNavigate } from 'react-router-dom';
import ModalPopup from './page-components/modals/modal-popup';
/* import axios from 'axios'; */

function PageLogin() {

    const navigate = useNavigate()
    const curuser = useContext(Context)
    /* console.log(curuser.user.isAuth) */

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [isModal, setIsModal] = useState(false)

    const logIn = async () => {
        const response = await login(username, password);
        console.log(response);
        console.log(response.data.LoginStatus);
        curuser.user.setIsAuth(response.data.LoginStatus)
        console.log (curuser.user.isAuth);
        if (response.data.LoginStatus === false) {
            setIsModal(true)
        } else {
            navigate('/Home')
            window.location.reload();
        }

        console.log("Yes response")  ;
        
    }
    
    return ( 
        <Container className="mt-5 d-flex justify-content-center align-items-center">
            <Card style={{width: 600}} className='p-5'>
                {/*           */}
                <Form >
                    <Form.Group className="mb-3" controlId="formBasicEmail">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control type="email" placeholder="Enter email" 
                            value={username}
                            onChange={e => setUsername(e.target.value)}
                        />
                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formBasicPassword">
                        <Form.Label>Password</Form.Label>
                        <Form.Control  type="password" placeholder="Password" 
                        value={password}
                        onChange={e => setPassword(e.target.value)}
                        />
                    </Form.Group>
                    <Button variant="primary" onClick={logIn}>
                        Login
                    </Button>
                </Form>
            </Card>
            { isModal === true ? <><ModalPopup heading={`!`} body={`   `} show={true}/></>
            : <></>
            }
        </Container>
     );
}

export default PageLogin;