import React, { useContext, useState } from 'react';
import { Nav, NavLink } from 'react-bootstrap'
import { Navbar, NavDropdown } from 'react-bootstrap';
import { Container } from 'react-bootstrap';
import { Image } from 'react-bootstrap';
import { Button } from 'react-bootstrap';
import { Context } from '../..';
import { useLocation, useNavigate } from "react-router-dom";



/*   navbar            */
function ComponentNavbar() {
    const curuser = useContext(Context)
    const [isLoggedIn, setIsLoggedIn] = useState(curuser.user.isAuth)

    const location = useLocation();
    console.log(location);

    const isLocation = location.pathname === "/Login"
    console.log("islocation " + isLocation);
    const navigate = useNavigate()
    
    const logOut = () => {
        if (curuser.user.isAuth === 'true') {
            curuser.user.setIsAuth('false')
            setIsLoggedIn('false')
            navigate('/Home')
            window.location.reload();
        } if (curuser.user.isAuth === 'false') {
            console.log("already false");
        }
        
        console.log(typeof(curuser.user.isAuth));
        console.log("isloggedin OOB " + curuser.user.isAuth, isLoggedIn);
    }
    console.log("isloggedin(localStore) isLoggedin (const) " + curuser.user.isAuth, isLoggedIn);

    return ( 
    <Navbar className='Navbar' bg='light' expand='lg'>
        <Image style={{width: '60px', marginLeft:'20px'}} src='https://svgshare.com/i/sK7.svg'/>
        <Container >
            <Navbar.Brand style={{fontFamily:'Montserrat', fontWeight:'bolder', color:'#99252b'}} href='Home'></Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav"/>
            <Navbar.Collapse id="basic-navbar-nav">
                <NavDropdown className='NavLink' title=" " id="basic-nav-dropdown">
                    <NavDropdown.Item href='/Rooms'>
                        <NavLink href='/Rooms'></NavLink>
                    </NavDropdown.Item>
                    <NavDropdown.Item href='/Rooms'>
                        <NavLink href='/Rooms'></NavLink>
                    </NavDropdown.Item>
                    <NavDropdown.Item href='/Rooms'>
                        <NavLink href='/Rooms'></NavLink>
                    </NavDropdown.Item>
                </NavDropdown>
                <NavDropdown className='NavLink' title=" " id="basic-nav-dropdown">
                    <NavDropdown.Item href='/Home'>
                        <NavLink href='/Home'></NavLink>
                    </NavDropdown.Item>
                    <NavDropdown.Item href='/Home'>
                        <NavLink href='/Home'></NavLink>
                    </NavDropdown.Item>
                    <NavDropdown.Item href='/Home'>
                        <NavLink href='/Home'></NavLink>
                    </NavDropdown.Item>
                </NavDropdown>
                <NavLink className='NavLink' href='/Homes'> </NavLink>
                <NavDropdown className='NavLink'  title=" " id="basic-nav-dropdown">
                    <NavDropdown.Item href='/Holdings'>
                        <NavLink href='/Holdings'></NavLink>
                    </NavDropdown.Item>
                    <NavDropdown.Item href='/Holdings'>
                        <NavLink href='/Holdings'></NavLink>
                    </NavDropdown.Item>
                </NavDropdown>
            </Navbar.Collapse>
        </Container>
        <Nav.Link style={{paddingRight:'20px'}} className='NavLink' href='/Contacts'></Nav.Link>
        { isLocation ? <div></div>
        : <div> { isLoggedIn === 'true' ?
            <div className='d-flex flex-direction-row align-items-center justify-content-around'>
            <NavLink className='NavLink d-flex m-2' href='/Admin'></NavLink>
            <Button className='m-2' style={{backgroundColor:'#99252b', borderColor:'#99252b'}} variant="primary" onClick={logOut}></Button>
            </div>
        : <Nav.Link className='LoginLink' href='/Login'><Button style={{backgroundColor:'#99252b', borderColor:'#99252b'}} variant="primary"></Button></Nav.Link>
        } </div>
        }
        
    </Navbar>
        );
}

export default ComponentNavbar;