import {makeAutoObservable} from "mobx"

export default class UserStorage {
     constructor(){
        this._isAuth = false
        this._user = {}
        makeAutoObservable(this)
     }

     setIsAuth (bool) {
        this._isAuth = bool
        localStorage.setItem('isAuth', bool)
     }
     setUser (user){
        this._user = user
     }

     get isAuth (){
        return localStorage.getItem('isAuth')
     }

     get user (){
        return this._user
     }
}