import { $host } from "./index";

const getAllServices = async() => {
    const response = await $host.get('api/Services')
    console.log(response);
    return response
}

const getServicebyID = async(id) => {
    const response = await $host.get('api/Services', {id})
    console.log(response);
    return response
}

const postService = async (service_name, service_price, service_img_url) => {
    const response = await $host.post('api/Services', {service_name, service_price,service_img_url})
    console.log(response);
    return response
}

const deleteServicebyID = async(id) => {
    const response = await $host.post('api/Services/Delete', {id})
    console.log(typeof(id));
    console.log(response);
    return response
}

export {getServicebyID, postService, getAllServices, deleteServicebyID}